import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { FingerprintComponent } from '../fingerprint/fingerprint.component';
import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ThemeService } from '../../../shared/services/theme.service';
import { LayoutService } from '../../../shared//services/layout.service';
import { v4 as uuid } from 'uuid';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {

  signinForm: FormGroup;
  hide=true;
  themeValue: any;
  browserKey: any;
  constructor(private fb: FormBuilder, 
    private layout: LayoutService,
    private router: Router, 
    private jwtService: JwtAuthService,
    public themeService: ThemeService, 
    public dialog: MatDialog,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.browserKey = uuid();
    this.signinForm = this.fb.group({
        username: ['', Validators.required],
        password: ['', Validators.required],
        systemCode: [ this.browserKey ]
      });
  }

  onSubmit() {
    if (!this.signinForm.invalid) {
      this.spinner.show();
      this.jwtService.loginIcut(this.signinForm.value).subscribe((res: any) => {
        console.log("ress", res);
        this.spinner.hide();
        if (res) {
          console.log("if res", res);
          this.jwtService.setUserAndToken(res, true);
          localStorage.setItem('branchName', res.branchName);
          localStorage.setItem('userName', res.userName);
          localStorage.setItem("browserId", this.browserKey);
          this.router.navigateByUrl('/others/dashboard');
          this.showToast(res);
        }
      }, (err: any) => {
        console.log("err", err);
        this.spinner.hide();
        if (err.error) {
          this.toastr.error(`${err.error.message}`,`Error!`,{
            progressAnimation:'decreasing',
            progressBar:true,
            positionClass:'toast-top-right',
            timeOut: 3000,
          });
        }
      });
    }
  }
  onChange(e): void {
    console.log("e", e);
    //  if (e.checked) {
    //   this.themeService.changeTheme('egret-navy','egret-dark-purple');
    // } 
    // else {
    //     this.themeService.changeTheme('egret-dark-purple','egret-navy');
    //   }
    // }
  /*  if (this.themeValue.value == 'light') {
      console.log("inside if");
      this.themeService.changeTheme1('egret-navy');
    } */
  }
  changeTheme1(theme) {
    //this.layout.publishLayoutChange({ matTheme: theme.name }) 
      this.layout.applyMatTheme(theme);
  }

  openDialog() {
    const dialogRef = this.dialog.open(FingerprintComponent);

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

 showToast(user:any){
  this.toastr.success(`${user.email}`,`Welcome, ${user.userName}!`,{
    progressAnimation:'decreasing',
    progressBar:true,
    positionClass:'toast-top-right',
    timeOut: 3000,
  });
 }

}